<template>
  <div>
    <el-form class="form" ref="form" :model="form" :rules="rules">
      <h1 class="title"></h1>

      <el-form-item label="" prop="username">
        <el-input v-model="form.username" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="form.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model="form.password" placeholder="" show-password></el-input>
      </el-form-item>
      <el-form-item label="" prop="checkpassword">
        <el-input type="password" v-model="form.checkpassword" placeholder="" show-password></el-input>
      </el-form-item>

      <el-form-item></el-form-item>

      <el-form-item>
        <el-button style="width: 100%" type="success" :loading="loading" @click.native="signup"></el-button>
      </el-form-item>
      <el-form-item>
        <router-link to="/">
          <el-link type="success" style="width: 48%;text-align: left"></el-link>
        </router-link>
        <el-link type="danger" @click.native="exit" style="width: 48%;text-align: right"></el-link>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>
  import {setToken, setUserInfo} from "../../utils/localStauts";
  import {signUp} from "../../api/user";
  import {Message} from "element-ui"

  export default {
    name: "SignUp",
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value.length<5) {
          callback(new Error('5'))
        } else {
          if (this.form.checkpassword !== '') {
            this.$refs.form.validateField('checkpassword');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.form.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };

      return {
        loading:false,
        form: {
          username: '',
          password: '',
          checkpassword: '',
          name: ''
        },
        rules: {
          username: [
            {max: 12, min: 5, message: ' 5  12 ', trigger: 'blur'},
            {required: true, message: '', trigger: 'blur' },
          ],
          password: [
            {validator: validatePass, trigger: 'blur'},
          ],
          name: [
            {min: 2, message: ' 2 ', trigger: 'blur'},
            {required: true, message: '', trigger: 'blur' },
          ],
          checkpassword: [
            {validator: validatePass2, trigger: 'blur'}
          ]
        }
      }
    }, methods: {
      signup() {
        this.loading=true
        //
        this.$refs.form.validate((valid) => {
          if (valid) {
            signUp(this.form).then(res => {
              Message.success("")
              this.loading=false
              this.$router.push("/")
            }).catch(e=>{
              this.loading=false
            })
          } else {
            this.loading=false
            return false;
          }
        });
      },
      exit() {
        window.location.href = "http://www.baidu.com";
      }
    }
  }
</script>

<style scoped>
  .form {
    margin: 0 auto;
    text-align: center;
    height: 400px;
    width: 580px;
  }

</style>



